import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import Main from './Containers/main';

export default () =>
  (<BrowserRouter>
    <Switch>
      <Route path="/" exact render={props => <Main {...props} />} />
    </Switch>
  </BrowserRouter>);



